.inputTab {
    text-align: center;
    height: 30px;
    border: none;
    font-size: 20px;
    font-weight: 500;
    width: 5cm;
    margin-top: 8px;

}

.navmain{
    background-color: blue;
    height: 80px;
    width: 100%;

}

.logo{
    color: white;
    font-size: 35px;
    line-height: 80px;
    padding: 0 100px;
    font-weight: bold;

}

.navmain ul{
    float: right;
    margin-right: 20px;
}
.navmain ul li {
    display: inline-block;
    line-height: 80px;
    margin: 0 5px;
}
.navmain ul li a{
    color: white;
    font-size: 17px;
    text-transform: uppercase;
    border-radius: 3px;
}

.login {
    display: inline-block;

    margin: 0 5px;
    color: black;
    width: 2cm;
    height: 1cm;

}
.SignUp{
    display: inline-block;

    margin: 0 5px;
    color: black;
    width: 2cm;
    height: 1cm;

}

a.active,a:hover{
    transition: .5s;

}
.SignUp:hover{
    background-color: blue;
}
.login:hover{
    background-color: blue;
}
.checkbtn{
    font-size: 30px;
    color: white;
    float: right;
    line-height: 80px;
    margin-right: 40px;
}
